<template>
  <div class="integral-view">
    <d-operation-box
      v-model="selectList"
      @delete="searchResultDeal"
      @optionClick="operationClick"
    >
      <d-input-search
        :max-length="40"
        placeholder="请输入积分兑换单号"
        @search="searchResultDeal"
        :width="264"
        ref="keywordsO"
        resultName="keywords"
        allowClear
        class="margin-right-16 redemption"
      >
        <a-button slot="enterButton"> 搜索 </a-button>
      </d-input-search>
      <d-mix-search
        name="address"
        @change="searchResultDeal"
        placeholder="客户"
        resultName="customer"
        ref="customerO"
        class="margin-right-16"
        :width="120"
      />
      <d-mix-search
        name="product"
        @change="searchResultDeal"
        placeholder="商品"
        resultName="product"
        ref="productO"
        class="margin-right-16"
        :width="120"
        :dialog-config="{
          rules: {
            type: 6,
          },
        }"
      />
      <d-mix-select
        :type="8"
        @change="searchResultDeal"
        allowClear
        resultName="billStatus"
        ref="billStatusO"
        :width="120"
      />
    </d-operation-box>
    <s-table
      :data-source="tableData"
      :rowSelection="rowSelection"
      :rowKey="selectionContent.primaryKey"
      :selectOnIndeterminate="selectOnIndeterminate"
      @change="tableChange"
      :pagination="pagination"
      :scroll="{
        x: 1300,
        y: `calc(100vh - ${calcHeight}px)`,
        scrollToFirstRowOnChange: true,
      }"
      class="a-table-common"
    >
      <a-table-column
        key="index"
        :width="82"
        class="center-text"
        align="center"
      >
        <template slot-scope="text, row, index">
          {{ (pagination.current - 1) * pagination.pageSize + index + 1 }}
        </template>
      </a-table-column>
      <a-table-column
        key="keywords"
        data-index="number"
        title="单号"
        :sorter="true"
        class="order-no"
      >
        <d-filter-dropdown
          slot="filterDropdown"
          slot-scope="{
            setSelectedKeys,
            selectedKeys,
            confirm,
            clearFilters,
            column,
          }"
          name="input"
          ref="keywordsH"
          :data="{
            setSelectedKeys,
            selectedKeys,
            confirm,
            clearFilters,
            column,
          }"
          @confirm="searchResultDeal"
        />
      </a-table-column>
      <a-table-column key="time" data-index="time" title="兑换时间">
        <d-filter-dropdown
          slot="filterDropdown"
          slot-scope="{
            setSelectedKeys,
            selectedKeys,
            confirm,
            clearFilters,
            column,
          }"
          name="range"
          ref="timeH"
          :data="{
            setSelectedKeys,
            selectedKeys,
            confirm,
            clearFilters,
            column,
          }"
          @confirm="searchResultDeal"
        />
      </a-table-column>
      <a-table-column
        key="customer"
        data-index="customer"
        title="客户"
        :sorter="true"
        :filter-dropdown-visible.sync="filterDropdownVisible.customer"
      >
        <d-filter-dropdown
          slot="filterDropdown"
          slot-scope="{
            setSelectedKeys,
            selectedKeys,
            confirm,
            clearFilters,
            column,
          }"
          name="customer"
          ref="customer"
          :data="{
            setSelectedKeys,
            selectedKeys,
            confirm,
            clearFilters,
            column,
          }"
          @confirm="searchResultDeal"
          :visible.sync="filterDropdownVisible.customer"
        />
      </a-table-column>
      <a-table-column
        key="exchangeScore"
        data-index="exchangeScore"
        title="兑换积分"
        :sorter="true"
        class="redeem"
      ></a-table-column>
      <a-table-column
        key="billStatus"
        data-index="billStatus"
        title="单据状态"
        :sorter="true"
      >
        <d-filter-dropdown
          slot="filterDropdown"
          slot-scope="{
            setSelectedKeys,
            selectedKeys,
            confirm,
            clearFilters,
            column,
          }"
          name="bill"
          ref="billStatusH"
          :data="{
            setSelectedKeys,
            selectedKeys,
            confirm,
            clearFilters,
            column,
          }"
          @confirm="searchResultDeal"
        />
      </a-table-column>
      <a-table-column
        key="user"
        data-index="user"
        title="兑换人"
      ></a-table-column>
      <a-table-column
        key="handler"
        data-index="handler"
        title="经手人"
        :filter-dropdown-visible.sync="filterDropdownVisible.handler"
      >
        <d-filter-dropdown
          slot="filterDropdown"
          slot-scope="{
            setSelectedKeys,
            selectedKeys,
            confirm,
            clearFilters,
            column,
          }"
          name="handler"
          ref="handlerH"
          :data="{
            setSelectedKeys,
            selectedKeys,
            confirm,
            clearFilters,
            column,
          }"
          @confirm="searchResultDeal"
        />
        <!-- <d-filter-dropdown
          slot="filterDropdown"
          slot-scope="{ setSelectedKeys, selectedKeys, confirm, clearFilters, column }"
          name="preparer"
          ref="handler"
          :data="{ setSelectedKeys, selectedKeys, confirm, clearFilters, column }"
          @confirm="searchResultDeal"
          :visible.sync="filterDropdownVisible.handler"
        /> -->
      </a-table-column>
      <a-table-column
        key="businessManager"
        data-index="businessManager"
        title="业务经理"
      >
        <d-filter-dropdown
          slot="filterDropdown"
          slot-scope="{
            setSelectedKeys,
            selectedKeys,
            confirm,
            clearFilters,
            column,
          }"
          name="handler"
          ref="businessManager"
          :data="{
            setSelectedKeys,
            selectedKeys,
            confirm,
            clearFilters,
            column,
          }"
          @confirm="searchResultDeal"
        />
      </a-table-column>
      <a-table-column key="syncStatus" data-index="syncStatus" title="同步状态">
        <d-filter-dropdown
          slot="filterDropdown"
          slot-scope="{
            setSelectedKeys,
            selectedKeys,
            confirm,
            clearFilters,
            column,
          }"
          name="input"
          ref="syncStatusH"
          :data="{
            setSelectedKeys,
            selectedKeys,
            confirm,
            clearFilters,
            column,
          }"
          @confirm="searchResultDeal"
        />
        <template slot-scope="text">
          <div class="sync">
            <div
              :class="
                text == 1
                  ? 'sync-yuan'
                  : text == 2
                  ? 'sync-yuan1'
                  : 'sync-yuan2'
              "
            ></div>
            <span class="sync-text">{{
              text == 1
                ? "未同步"
                : text == 2
                ? "ERP草稿"
                : text == 3
                ? "ERP删除"
                : "同步失败"
            }}</span>
            <icon-font-box
              type="icontishi"
              size="14"
              height="14"
              class="icon-5"
            />
          </div>
          <!-- {{ text.syncStatus | balance(1) }} -->
        </template>
      </a-table-column>
      <a-table-column key="action" title="操作">
        <!-- eslint-disable-next-line vue/no-unused-vars -->
        <template slot-scope="text">
          <a-button class="details"> 详情 </a-button>
          <a-button class="print"> 打印 </a-button>
          <a-button class="cancel"> 取消 </a-button>
        </template>
      </a-table-column>
      <template #footer>
        <div class="footer">
          <div class="footer-he">合计</div>
          <div class="footer-ji">12551223积分</div>
        </div>
      </template>
    </s-table>
    <d-more-search
      :visible.sync="moreVisible"
      @confirm="moreSearchResultDeal"
      ref="moreSearch"
      :rulesList="[
        {
          type: 'timeRange',
          label: '录单时间',
          field: 'timeRange',
          placeholder: '请选择时间',
        },
        {
          type: 'numberRange',
          label: '采购金额',
          field: 'amount',
          placeholder: '请输入金额',
        },
        {
          type: 'mixSearch',
          mixType: 'handler',
          label: '经手人',
          field: 'handler',
        },
        {
          type: 'mixSearch',
          mixType: 'preparer',
          label: '制单人',
          field: 'preparer',
        },
        {
          type: 'mixSelect',
          mixType: 'print',
          label: '打印状态',
          field: 'print',
        },
      ]"
    />
  </div>
</template>

<script>
import {
  STable,
  TableSelection,
  SearchFilter,
  DInputSearch,
  DMixSearch,
  DMixSelect,
  DOperationBox,
  DFilterDropdown,
  DMoreSearch,
  IconFontBox,
} from "@handday/components";
export default {
  name: "IntegralIndex",
  components: {
    DOperationBox,
    DMixSearch,
    DMixSelect,
    IconFontBox,
    STable,
    DFilterDropdown,
    DMoreSearch,
    DInputSearch,
  },
  mixins: [TableSelection, SearchFilter],
  provide() {
    return {
      selectionContent: this.selectionContent,
      pagination: this.pagination,
    };
  },
  data() {
    return {
      searchResult: {
        keywords: undefined,
        number: undefined,
        sort: undefined,
        customer: undefined,
        product: undefined,
        billStatus: undefined,
        startTime: undefined,
        endTime: undefined,
        handler: undefined,
        amount: undefined,
        preparer: undefined,
      },
      tableLoading: false,
      selectConfig: {
        keywords: {
          cn: ["单号"],
          en: ["keywords"],
        },
        time: {
          cn: ["时间"],
          en: ["startTime", "endTime"],
          link: ["开始", "至", "结束"],
          dataKey: "value",
          valueKey: "data",
        },
        customer: {
          cn: ["客户"],
          en: ["customer"],
          dataKey: "name",
          valueKey: "businessId",
          dataTarget: "rows",
          valueTarget: "rows",
        },
        product: {
          cn: ["商品"],
          en: ["product"],
          dataKey: "name",
          valueKey: "id",
        },
        billStatus: {
          cn: ["单据状态"],
          en: ["billStatus"],
          dataKey: "name",
          valueKey: "id",
          dataTarget: "rows",
          valueTarget: "rows",
        },
        handler: {
          cn: ["经手人"],
          en: ["handler"],
          dataKey: "name",
          valueKey: "stafferId",
        },
        preparer: {
          cn: ["制单人"],
          en: ["preparer"],
          dataKey: "name",
          valueKey: "stafferId",
        },
        amount: {
          cn: ["金额"],
          en: ["amount1", "amount2"],
          link: ["开始", "至", "结束"],
          linkMode: "after",
        },
      },
      filterSortConfig: {
        number: 1,
      },
      filterDropdownVisible: {
        handler: false,
        customer: false,
      },
      moreVisible: false,
      searchValue: "",
    };
  },
  created() {
    this.selectionContent.primaryKey = "id";
    this.loadTableData();
  },
  computed: {
    /** 计算表格高度 */
    calcHeight() {
      if (Object.keys(this.selectList).length) return 392;
      else return 344;
    },
  },
  methods: {
    loadTableData(page = this.pagination) {
      console.log("检索条件：", this.searchResult);
      this.tableLoading = true;
      this.tableData = [];
      for (let i = 0; i < page.pageSize; i++) {
        this.tableData.push({
          id: `${i}${page.current}${page.pageSize}`,
          key: `${i}${page.current}${page.pageSize}`,
          number: "JF-20210705-0001",
          time: "2020/09/22 23:45",
          customer: "书亦烧仙草(环球中心店)",
          exchangeScore: "1280积分",
          billStatus: "已完成",
          user: "徐永松",
          handler: "徐永松2",
          businessManager: "徐永松3",
          syncStatus: i % 3,
        });
      }
      console.log("page", page);
      this.dealSelectionCheckBox();
      this.pagination.current = page.current;
      this.pagination.total = 400;
      this.tableLoading = false;
    },
    operationClick(result) {
      console.log(result);
      if (result.key === "more") {
        this.moreVisible = true;
      }
      if (result.key === "clear") {
        this.handleSelectedClear();
      }
    },
    /**
     * 前置处理
     * */
    beforeSearchChange(result) {
      if (result.name === "number") {
        this.searchValue = "";
        return true;
      } else {
        return true;
      }
    },
    /**
     * 单号搜索
     * */
    onSearch(val) {
      this.searchResult.number = val;
      this.selectList = {
        ...this.selectList,
        number: {
          label: "单号",
          value: val,
          key: "number",
        },
      };
      this.$refs[`numberH`] && this.$refs[`numberH`].resetData();
    },
    /**
     * 条件变更
     * */
    changeHandle() {},
  },
};
</script>

<style lang="less" scoped>
.integral-view {
  /deep/.operation-index {
    .filter-list {
      .margin-right-16 {
        margin-right: 16px;
      }
      .redemption {
        border-radius: 2px;
        overflow: hidden;
        border: 1px solid #d9d9d9;
        .ant-input,
        .ant-btn {
          border: 0;
        }
        .ant-btn {
          width: 64px;
          letter-spacing: -2px;
          box-sizing: border-box;
          background: #f8f8f9;
          font-size: 14px;
          font-family: PingFangSC, PingFangSC-Regular;
          font-weight: 400;
          text-align: left;
          border-radius: 0;
          color: #333333;
          border-left: 1px solid #d9d9d9;
          line-height: 22px;
        }
      }
    }
    .more-search {
      font-size: 14px;
      font-family: PingFangSC, PingFangSC-Regular;
      display: flex;
      align-items: center;
      .icon-font-wrap {
        font-size: 12px !important;
      }
      .more-search a {
        display: flex;
        align-items: center;
      }
    }
  }
  /deep/.ant-spin-nested-loading {
    .ant-table-thead > tr > th,
    .ant-table-tbody > tr > td {
      padding: 0 16px;
      height: 44px;
      line-height: 44px;
    }
  }
  /deep/.a-table-common {
    .order-no {
      color: #378efb;
    }
    .sync {
      display: flex;
      align-items: center;
      .icon-5 {
        color: #378efb !important;
        margin-left: 5px;
      }
      .sync-yuan,
      .sync-yuan1,
      .sync-yuan2 {
        width: 8px;
        height: 8px;
        border-radius: 50%;
        margin-right: 8px;
        background: #378efb;
      }
      .sync-yuan1 {
        background: #52c41a;
      }
      .sync-yuan2 {
        background: #f5222d;
      }
    }
    .ant-table-thead > tr > th {
      font-size: 14px;
      font-family: PingFangSC, PingFangSC-Medium;
      font-weight: 500;
      text-align: left;
      color: #262626;
    }
    .sync {
      display: flex;
      align-items: center;
      .sync-yuan,
      .sync-yuan1,
      .sync-yuan2,
      .sync-yuan3 {
        width: 8px;
        height: 8px;
        border-radius: 50%;
        margin-right: 8px;
        background: #378efb;
      }
      .sync-yuan1 {
        background: #52c41a;
      }
      .sync-yuan2 {
        background: #bbbbbb;
      }
      .sync-yuan3 {
        background: #f5222d;
      }
    }
    /** 统计行 START */
    .footer-stats-table {
      flex-shrink: 0;

      /deep/.ant-table-thead {
        display: none;
      }
      /deep/.ant-table-small {
        border-left: none;
        border-right: none;
        border-radius: initial;
      }
      /deep/.total-row {
        background-color: #fff;
      }
      /deep/.ant-table-body {
        background-color: rgb(250, 250, 250);
      }
    }
    .details,
    .print,
    .cancel {
      font-size: 14px;
      font-family: PingFangSC, PingFangSC-Regular;
      font-weight: 400;
      // text-align: center;
      color: #378efb;
      line-height: 22px;
      border: 0;
      padding: 0;
      background: rgba(0, 0, 0, 0);
      letter-spacing: -2px;
    }
    .details {
      margin-right: 20px;
    }
    .print {
      margin-right: 14px;
    }
    .footer {
      display: flex;
      align-items: center;
      div {
        width: 50%;
        font-size: 14px;
        font-family: PingFangSC, PingFangSC-Regular;
        font-weight: 400;
        color: #595959;
        line-height: 22px;
      }
    }
  }
  /deep/.ant-table-row:last-child td {
    border: 0;
  }
  /deep/.ant-table-footer {
    border-top: 2px solid #d9d9d9;
  }
  /deep/.ant-table-body {
    overflow: auto !important;
  }
}
</style>
